<template>
  <v-container
    id="regular-tables-view"
    fluid
    tag="section"
  >
    <material-card
      icon="mdi-clipboard-text"
      icon-small
      color="primary"
      title="Marbete"
    >
      <v-data-table
        :headers="headers"
        :items="lista_kardex"
        sort-by=""
        class="elevation-1"
        :loading="cargando"
        loading-text="Cargando... Espere por favor"
        no-data-text="No existen datos"
        no-results-text="No se encontraron registros coincidentes"
        :search="search"
        :footer-props="{
          showFirstLastPage: true,
          firstIcon: 'mdi-arrow-collapse-left',
          lastIcon: 'mdi-arrow-collapse-right',
          prevIcon: 'mdi-arrow-left',
          nextIcon: 'mdi-arrow-right',
          'items-per-page-text':'Items por pgina'
        }"
      >
        <template v-slot:top>
          <v-toolbar
            flat
          >
            <v-text-field
              v-model="search"
              append-icon="mdi-magnify"
              label="Bsqueda"
              single-line
              hide-details
            />
            <v-spacer />
            <v-dialog
              v-model="dialog"
              max-width="500px"
            >
              <v-card>
                <v-card-title>
                  <span class="text-h5">{{ formTitulo }}</span>
                </v-card-title>
                <v-card-text>
                  <v-form ref="form" v-model="valid">
                    <v-container>
                      <!-- {{marbete}} -->
                      <v-row>
                        <v-col
                        cols="12"
                        sm="12"
                        md="6"
                        >
                          <v-text-field
                            v-model="marbete.numeroMarbete"
                            label="N de Marbete"
                            :counter="50"
                            :rules="fieldRules"
                            :autofocus="true"
                            hint="Introduzca el numero"
                            persistent-hint
                          />
                        </v-col>
                        <v-col
                          cols="12"
                          sm="12"
                          md="6"
                        >
                          <v-menu
                            ref="menu1"
                            v-model="menu1"
                            :close-on-content-click="false"
                            :nudge-right="40"
                            transition="scale-transition"
                            offset-y
                          >
                            <template v-slot:activator="{ on, attrs }">
                              <v-text-field
                                v-model="marbete.fecha"
                                label="Fecha Marbete"
                                prepend-icon="mdi-calendar"
                                readonly
                                v-bind="attrs"
                                v-on="on"
                              ></v-text-field>
                            </template>
                            <v-date-picker
                              v-model="marbete.fecha"
                              @input="menu1 = false"
                            >
                            </v-date-picker>
                          </v-menu>
                        </v-col>
                        <v-col
                          cols="12"
                          sm="12"
                          md="12"
                        >
                          <v-text-field
                            v-model="marbete.lugarTrabajo"
                            label="Lugar de Trabajo"
                            :counter="50"
                            :rules="fieldRules"
                            hint="Introduzca el lugar"
                            persistent-hint
                          />
                        </v-col>
                      </v-row>
                    </v-container>
                  </v-form>
                </v-card-text>
                <v-card-actions>
                  <v-spacer />
                  <v-btn
                    text
                    @click="close"
                  >
                    Cancelar
                  </v-btn>
                  <v-btn
                    :loading="estadoBoton"
                    text
                    @click="guardar"
                    :disabled="!valid"
                  >
                    Guardar
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
            <v-dialog
              v-model="dialogDelete"
              max-width="500px"
            >
              <v-card>
                <v-card-title class="text-h5">
                  Est seguro de eliminar el registro?
                </v-card-title>
                <v-spacer />
                <v-card-subtitle class="text-h6">
                  {{ kardex.Persona.ci + ' - ' + kardex.Persona.paterno + ' ' + kardex.Persona.materno + ' ' + kardex.Persona.nombre }}
                </v-card-subtitle>
                <v-card-actions>
                  <v-spacer />
                  <v-btn
                    color="blue darken-1"
                    text
                    @click="closeEliminar"
                  >
                    Cancelar
                  </v-btn>
                  <v-btn
                    color="red darken-1"
                    text
                    @click="eliminarConfirm"
                  >
                    Eliminar
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:[`item.acciones`]="{ item }">
          <v-btn
          
            color="primary"
            dark
            class="ma-1"
            @click="editar(item)"
          >
            Nuevo Marbete
            <v-icon
              dark
              small
              class="ma-1"
            >
              mdi-text
            </v-icon>
          </v-btn>
        </template>
      </v-data-table>
    </material-card>
    <v-snackbar
      v-model="snackbar"
      :timeout="timeout"
      :color="color"
    >
      {{ text }}

      <template v-slot:action="{ attrs }">
        <v-btn
          color=""
          text
          v-bind="attrs"
          @click="snackbar = false"
        >
          Cerrar
        </v-btn>
      </template>
    </v-snackbar>
  </v-container>
</template>

<script>
import * as kardexService from "../services/kardexService"
import * as marbeteService from "../services/marbeteService"
  export default {
    data () {
      return {
        estadoBoton: false,
        dialog: false,
        dialogDelete: false,
        lista_marbete: [],
        lista_kardex: [],
        cargando: true,
        marbete: {
          afiliadoId: ''
        },
        kardex: {
          Persona: {ci:'', paterno:'', materno:'', nombre:''}
        },
        search: '',
        headers: [
          {
            text: 'ID',
            align: 'start',
            sortable: false,
            value: 'id',
          },
          { text: 'Departamento', value: 'Distrito.descripcion' },
          { text: 'C.I.', value: 'Persona.ci' },
          { text: 'Nombre Completo', value: 'Persona.nombreCompleto' },
          { text: 'Profesin', value: 'Profesion.descripcion' },
          { text: 'Mat. Col.', value: 'numeroInscripcion' },
          { text: 'Mat. Prof.', value: 'numeroMinisterio' },
          { text: 'Acciones', value: 'acciones', sortable: false, align: 'center' },
        ],
        editedIndex: -1,
        defaultMarbete: {
          // descripcion: '',
          // categorias: [
          //   { categoriaId: '' },
          // ],
        },
        color: '',
        snackbar: false,
        text: '',
        timeout: 3000,
        valid: true,
        fieldRules: [
          v => !!v || 'El campo es requerido',
          v => (v && v.length <= 50) || 'El campo debe ser menor a 50 caracteres',
        ],
        menu1: false,
      }
    },
    computed: {
      formTitulo () {
        return this.editedIndex === -1 ? 'Nuevo Registro' : 'Nuevo Registro'
      },
    },
    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeEliminar()
      },
    },
    async mounted () {
      // let datos = await kardexService.listar()
      // console.log(datos);
      // this.lista_kardex = datos
      // this.cargando = false
      this.obtener()
    },
    methods: {
      async obtener () {
        const res = await kardexService.listarMarbete()
        // console.log(datos)
        this.lista_kardex = res.data
        this.cargando = false
      },
      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.marbete = Object.assign({}, this.defaultMarbete)
          this.editedIndex = -1
        })
        this.estadoBoton = false
      },
      async guardar () {
        let res = null
        this.validate();
        this.estadoBoton = true
        if (this.editedIndex > -1) {
          const mar = this.lista_kardex[this.editedIndex]
          this.marbete.afiliadoId = mar.id
          res = await marbeteService.guardar(this.marbete)
          // const mar = this.lista_kardex[this.editedIndex]
          // res = await kardexService.modificar(this.marbete, mar.id)
          // Object.assign(this.lista_kardex[this.editedIndex], this.marbete)
        } else {
          const mar = this.lista_kardex[this.editedIndex]
          this.marbete.afiliadoId = mar.id
          res = await marbeteService.guardar(this.marbete)
          // console.log(res);
          // this.lista_kardex.push(this.marbete)
        }
        if(res.status == 200){
          this.text = "Registro guardado"
          this.snackbar = true
          this.color = "green"
        } else {
          this.text = res.data.message
          this.snackbar = true
          this.color = "dark-red"
        }
        this.obtener()
        this.estadoBoton = false
        this.close()
      },
      editar (item) {
        this.editedIndex = this.lista_kardex.indexOf(item)
        // this.marbete = Object.assign({}, item)
        this.dialog = true
      },
      detalle (item) {
        this.editedIndex = this.lista_kardex.indexOf(item)
        this.kardex = Object.assign({}, item)
        this.dialogDetalle = true
      },
      eliminar (item) {
        this.editedIndex = this.lista_kardex.indexOf(item)
        this.kardex = Object.assign({}, item)
        this.dialogDelete = true
      },
      async eliminarConfirm () {
        try {
          const mar = this.lista_kardex[this.editedIndex]
          const res = await kardexService.eliminar(mar.id)
          if(res.status == 200){
            this.text = res.data.message
            this.snackbar = true
            this.color = "green"
            this.lista_kardex.splice(this.editedIndex, 1)
          } else {
            this.text = res.data.message
            this.snackbar = true
            this.color = "dark-red"
          }
          // if (!res.error) {
          //   this.lista_kardex.splice(this.editedIndex, 1)
          // }
          this.closeEliminar()
        } catch (error) {
          console.log(error);
        }
      },
      closeEliminar () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.marbete = Object.assign({}, this.defaultMarbete)
          this.editedIndex = -1
        })
      },
      validate() {
        this.$refs.form.validate()
      }
    },
  }
</script>
